import { LayoutModule } from './../../../components/layout/layout.module';
import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';

import { KalenderRoutingModule } from './kalender-routing.module';
import { KalenderComponent } from './kalender.component';
import { PerfectScrollbarModule } from 'ngx-perfect-scrollbar';
import { WidgetsComponentsModule } from 'src/app/components/widgets/widgets-components.module';
import { NzLayoutModule, NgZorroAntdModule, NzRadioModule } from 'ng-zorro-antd';
import { ReactiveFormsModule, FormsModule } from '@angular/forms';
import { FormBuilderTypeSafe } from 'src/app/services/angular-reactive-forms-helper';


@NgModule({
    declarations: [KalenderComponent],
    imports: [
        CommonModule,
        KalenderRoutingModule,

        PerfectScrollbarModule,
        WidgetsComponentsModule,
        NzLayoutModule,
        LayoutModule,
        NgZorroAntdModule,
        ReactiveFormsModule,
        FormsModule,
        NzRadioModule,
    ],
    providers: [FormBuilderTypeSafe],
})
export class KalenderModule { }
