import { NgModule } from '@angular/core'
import { Routes, RouterModule } from '@angular/router'
import { KalenderComponent } from './kalender.component'

const routes: Routes = [
    {
        path: '',
        component: KalenderComponent,
        children: [
            {
                path: 'pengaturan/mastercuti',
                loadChildren: () =>
                    import('./pengaturan/mastercuti/mastercuti.module').then(
                        mo => mo.MastercutiModule,
                    ),
            },
            {
                path: 'pengaturan/masterlembur',
                loadChildren: () =>
                    import('./pengaturan/masterlembur/masterlembur.module').then(
                        mo => mo.MasterlemburModule,
                    ),
            },
            {
                path: 'pengaturan/jam-kerja',
                loadChildren: () =>
                    import('./pengaturan/jam-kerja/jam-kerja.module').then(
                        mo => mo.JamKerjaModule,
                    ),
            },
            {
                path: 'absensi',
                loadChildren: () => import('./absensi/absensi.module').then(mo => mo.AbsensiModule),
            },
            {
                path: 'cuti',
                loadChildren: () => import('./cuti/cuti.module').then(mo => mo.CutiModule),
            },
            {
                path: 'lembur',
                loadChildren: () => import('./lembur/lembur.module').then(mo => mo.LemburModule),
            },
            {
                path: 'perjalanan-dinas',
                loadChildren: () => import('./perjalanan-dinas/perjalanan-dinas.module').then(mo => mo.PerjalananDinasModule),
            },
            {
                path: 'kalender-perusahaan',
                loadChildren: () => import('./kalender-perusahaan/kalender-perusahaan.module').then(mo => mo.KalenderPerusahaanModule),
            },
            {
                path: 'pilot/absensi',
                loadChildren: () => import('./pilot/absensi/absensi.module').then(mo => mo.AbsensiModule),
            },
            {
                path: 'pilot/dvr',
                loadChildren: () => import('./pilot/dvr/dvr.module').then(mo => mo.DvrModule),
            },
            {
                path: 'pilot/summary',
                loadChildren: () => import('./pilot/summary/summary.module').then(mo => mo.SummaryModule),
            }
        ],
    },
]

@NgModule({
    imports: [RouterModule.forChild(routes)],
    exports: [RouterModule],
})
export class KalenderRoutingModule { }
