import { Component, OnInit } from '@angular/core';
import {
    slideFadeinUp,
    slideFadeinRight,
    zoomFadein,
    fadein,
} from 'src/app/layouts/App/router-animations'
import { Store, select } from '@ngrx/store'
import { RouterOutlet } from '@angular/router'
import { Observable } from 'rxjs'
import * as Reducers from 'src/app/store/reducers'
import { MenuService } from 'src/app/services/menu.service';

@Component({
    selector: 'app-kalender',
    templateUrl: './kalender.component.html',
    styleUrls: ['./kalender.component.scss'],
    animations: [],
})
export class KalenderComponent implements OnInit {

    settings$: Observable<any>
    menuLayoutType: string
    isContentNoMaxWidth: boolean
    isAppMaxWidth: boolean
    isGrayBackground: boolean
    isSquaredBorders: boolean
    isCardShadow: boolean
    isBorderless: boolean
    isTopbarFixed: boolean
    isGrayTopbar: boolean
    routerAnimation: string

    constructor(private store: Store<any>, private menuService: MenuService) {
        this.store.pipe(select(Reducers.getSettings)).subscribe(state => {
            this.menuLayoutType = state.menuLayoutType
            this.isContentNoMaxWidth = state.isContentNoMaxWidth
            this.isAppMaxWidth = state.isAppMaxWidth
            this.isGrayBackground = state.isGrayBackground
            this.isSquaredBorders = state.isSquaredBorders
            this.isCardShadow = state.isCardShadow
            this.isBorderless = state.isBorderless
            this.isTopbarFixed = state.isTopbarFixed
            this.isGrayTopbar = state.isGrayTopbar
            this.routerAnimation = state.routerAnimation
        })
    }

    menuData: any = []
    ngOnInit() {
        this.menuData = [

            {
                title: 'Leave',
                key: 'cuti',
                icon: 'fe fe-file-plus',
                url: '/hr/kalender/cuti',
            },

            {
                title: 'Overtime',
                key: 'lembur',
                icon: 'fe fe-clock',
                url: '/hr/kalender/lembur',
            },
            {
                title: 'Attendance',
                key: 'absensi',
                icon: 'fe fe-bell',
                count: 6,
                children: [
                    {
                        title: 'Activity',
                        key: 'aktifitas',
                        url: 'absensi/aktifitas',
                    },
                    {
                        title: 'Report',
                        key: 'laporan',
                        url: '/hr/kalender/module/absensi/laporan',
                    },
                    {
                        title: 'Policy',
                        key: 'jamkerja',
                        url: '/hr/kalender/module/absensi/kebijakan',
                    },
                ]
            },
            {
                title: 'Schedule',
                key: 'Operation Department',
                icon: 'fe fe-calendar',
                count: 6,
                children: [

                    {
                        title: 'Pilot',
                        key: 'pilot',
                        url: '/hr/kalender/pilot/absensi/list/hash_pilot',
                    },
                    {
                        title: 'Maintenance',
                        key: 'maintenance',
                        url: '/hr/kalender/pilot/absensi/list/hash_maintenance',
                    },
                    {
                        title: 'Operation',
                        key: 'operation',
                        url: '/hr/kalender/pilot/absensi/list/hash_operation',
                    },
                    // {
                    //     title: 'Maintenance',
                    //     key: 'maintenance',
                    //     url: '/hr/kalender/pilot/absensi/list/maintenance',
                    // },

                ]
            },
            {
                title: 'On Duty',
                key: 'perjalanan-dinas',
                icon: 'fe fe-navigation',
                url: '/hr/kalender/perjalanan-dinas',
            },
            {
                title: 'DFR',
                key: 'dvr',
                icon: 'fe fe-file-text',
                url: '/hr/kalender/pilot/dvr',
            },
            {
                title: 'Settings',
                key: 'pengaturan',
                icon: 'fe fe-settings',
                count: 6,
                children: [
                    {
                        title: 'Holiday',
                        key: 'kalender-perusahaan',
                        url: '/hr/kalender/kalender-perusahaan',
                    },
                    {
                        title: 'Overtime',
                        key: 'pengaturan/masterlembur',
                        url: '/hr/kalender/pengaturan/masterlembur',
                    },
                    {
                        title: 'Working Schedule',
                        key: 'pengaturan/jam-kerja',
                        url: '/hr/kalender/pengaturan/jam-kerja/list',
                    },
                    {
                        title: 'Leave',
                        key: 'pengaturan/mastercuti',
                        url: '/hr/kalender/pengaturan/mastercuti',
                    },


                ],
            },


        ]
        this.menuService.menu.next(this.menuData)
        this.menuService.subBar_info.next({ parent: 'HR', child: 'Kalender' })
    }

    routeAnimation(outlet: RouterOutlet, animation: string) {
        if (animation === this.routerAnimation) {
            return outlet.isActivated && outlet.activatedRoute.routeConfig.path
        }
    }


}
